<template>
    <div>
        <section id="login">
            <div class="input">
                <i class="fa-solid fa-user"></i>
                <input type="text" placeholder="아이디" v-model="id" />
            </div>
            <div class="input">
                <i class="fa-solid fa-key"></i>
                <input type="password" placeholder="비밀번호" v-model="pw" />
            </div>
            <div class="oval login" @click="login">로그인</div>
        </section>
        <section id="join">
            <div class="text">또는</div>
            <div class="btn">
                <button id="naver_id_login"></button>
                <button class="kakao" @click="kakaoLogin"></button>
                <button class="google" @click="kakaoLogin"></button>
            </div>
        </section>
    </div>
</template>

<script>
import auth from "@/api/auth.js";
import { mapMutations } from "vuex";
export default {
    data() {
        return {
            id: "",
            pw: "",
        };
    },
    mounted() {
        this.initNaver();
    },
    methods: {
        ...mapMutations(["SET_USER"]),
        initNaver() {
            const naver_id_login = new window.naver_id_login(
                "ZEBr31RYP_YsPp4wMrLq",
                "http://localhost:8080/login/naver"
            );
            const state = naver_id_login.getUniqState();
            naver_id_login.setButton("green", 1, 45);
            naver_id_login.setState(state);
            naver_id_login.setPopup();
            naver_id_login.init_naver_id_login();
        },
        kakaoLogin() {
            window.Kakao.Auth.login({
                success: () => {
                    window.Kakao.API.request({
                        url: "/v2/user/me",
                        success: ({ id }) => {
                            sessionStorage.setItem("id", `k_${id}`);
                            this.$router.push("/main").catch(() => {});
                        },
                        fail: (error) => {
                            console.log(error);
                        },
                    });
                },
                fail: (error) => {
                    console.log(error);
                },
            });
        },
        async login() {
            try {
                const { data } = await auth.login({
                    id: this.id,
                    pw: this.pw,
                });

                sessionStorage.setItem("user_id", data.user_id);
                alert("로그인 성공");
                this.$router.push("/main");
            } catch (error) {
                if (error.status === 401) {
                    alert(`유효하지 않은 ${error.data.error}`);
                } else {
                    console.log(error);
                }
            }
        },
    },
};
</script>

<style scoped>
.input {
    background: white;
    width: 300px;
    height: 40px;
    margin: 15px auto;
    border-radius: 20px;
    font-size: 20px;
}
.input i {
    font-size: 30px;
    line-height: 40px;
    margin: auto 20px;
    width: 30px;
    color: #757575;
}
.input:hover i {
    color: rgb(255, 123, 0);
}
.input input {
    position: relative;
    width: 130px;
    font-size: 20px;
    top: -4px;
    border: 0;
}
.input input:focus {
    outline: 0px;
}
.login {
    text-align: center;
    font-size: 28px;
    font-weight: bolder;
    width: 300px;
    height: 45px;
    line-height: 45px;
    border-radius: 23px;
    margin: 15px auto;
    margin-top: 30px;
    background: #ff7b00;
    cursor: pointer;
    color: white;
}
.login a {
    text-decoration: none;
    color: white;
}

#join {
    margin: 50px auto;
    width: 270px;
    text-align: center;
    border-top: 1px solid #757575;
}
#join .text {
    position: relative;
    font-size: 20px;
    font-weight: bolder;
    color: #757575;
    top: -14px;
    width: 85px;
    margin: 0 auto;
    background: #f9f2d3;
}
.btn {
    width: 270px;
    height: 50px;
    float: left;
    margin: 5px auto;
}
.btn button {
    width: 45px;
    height: 45px;
    border: none;
    border-radius: 25px;
    overflow: hidden;
}
.btn .kakao {
    background: url(../../public/img/btnKakao.PNG) center/cover;
    margin: 0 55px;
}
.btn .google {
    background: url(../../public/img/btnGoogle.PNG) center/cover;
}
</style>
